* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    list-style-type: none;
}

body {
    -webkit-font-smoothing: antialiased;
}

body,
input,
button {
    font-size: 15px;
    font-family: poppins;
}

button {
    cursor: pointer;
}

.vc {
    display: flex;
    align-items: center;
}

.logo {
    display: flex;
    flex-direction: row;
}

#logo {
    height: 45px;
    width: 45px;
    background: url(./logo192.png) no-repeat center center/contain;
}

#nav {
    background: rgb(16, 16, 48);
    justify-content: space-between;
    height: 45px;
    padding: 0px 30px;
    flex-direction: row;
}

#nav ul {
    display: flex;
    height: 45px;
    flex-direction: row;
    align-items: center;
}

.menu-item {
    text-decoration: none;
    margin: 0px 5px;
    color: wheat;
    border-bottom: 0px solid white;
}

.menu-item:hover {
    border-bottom: 2px solid white;
    border-radius: 2px;
}